import axios from 'axios'
import React, { Component } from 'react'
import { DivCard, DivTitle, NavButton2, SectCard } from '../styles/stylesGlobal'
const URL = 'http://localhost:4000/targetas'

export default class SectionCard extends Component {

    constructor() {
        super()
        this.state = {
            data: []
        }
    }

    componentDidMount() {
        this.Get()
    }

    Get = async () => {
        let { data } = await axios.get(URL);
        this.setState({
            data
        })
    }

    render() {
        return (
            <SectCard>
                <DivTitle>
                    <h1>We support your challenge in new areas.</h1>
                    <p>Our highly qualified headhunters, who have passed a rigorous screening process, will introduce you to your new career opportunity.</p>
                    <div>
                        <NavButton2>Try it Free</NavButton2>
                        <NavButton2>Explore</NavButton2>
                    </div>
                </DivTitle>

                <DivCard>
                    {
                        this.state.data.map((e) => {
                            const { id, image, title, subTitle, comment } = e;
                            return (
                                <section key={id} id={id}>
                                    <img src={image} alt='' />
                                    <h2>{title}</h2>
                                    <h4>{subTitle}</h4>
                                    <p>{comment}</p>
                                </section>
                            )
                        })
                    }
                </DivCard>
            </SectCard>
        )
    }
}
