export default class Navbar2 extends Component {
  render() {
    return (
      <div>
          <NavPincipal>
                <NavIMG src='https://i.ibb.co/jDmPtMp/logo.png' alt='' />
                <NavHome>
                    <Link to="/"><NavButton3>Home</NavButton3></Link>
                </NavHome>
                
            </NavPincipal>
      </div>
    )
  }
}
