import axios from 'axios'
import React, { Component } from 'react'
import { Input, SectCard2, SectInputCard, SectMain, SubSectCard2 } from '../styles/stylesGlobal'
const URL1 = 'http://localhost:4000/logos'
const URL2 = 'http://localhost:4000/targetas2'

export default class Main extends Component {

    constructor() {
        super()
        this.state = {
            arr1: [],
            arr2: []
        }
    }

    componentDidMount() {
        this.Get1()
        this.Get2()
    }

    Get1 = async () => {
        let { data } = await axios.get(URL1)
        this.setState({
            arr1: data
        })
    }

    Get2 = async () => {
        let { data } = await axios.get(URL2)
        this.setState({
            arr2: data
        })
    }

    render() {
        return (
            <main>
                <SectMain>
                    <p>Trusted by the world's leading companies</p>
                    {
                        this.state.arr1.map((e) => {
                            const { image, id } = e;
                            return (
                                <div key={id} id={id}>
                                    <img src={image} alt='' />
                                </div>
                            )
                        })
                    }
                </SectMain>

                <SectInputCard>
                    <div class="tituInput">
                        <h2>Search for your area of interest</h2>
                        <p>Please enter your desired industry, job title, and skills</p>
                        <section class="sectInput">
                            <div>
                                <img src='https://i.ibb.co/PMvRh7G/Frame-7.png' alt='' />
                                <form>
                                    <Input placeholder='engineer, Internet and Web Industry'/>
                                </form>
                            </div>
                            <button>search</button>
                        </section>
                    </div>

                    <SectCard2>
                        {
                            this.state.arr2.map((e) => {
                                const { id, title, location, work } = e;
                                return (
                                    <SubSectCard2 key={id} id={id}>
                                        <h2>{title}</h2>
                                        <div class="cards2">
                                            <section class="subCards2">
                                                <img src='https://i.ibb.co/j4GxhC3/Vector.png' alt='' />
                                                <h4>{location}</h4>
                                            </section>
                                            <section class="subCards2">
                                                <img src='https://i.ibb.co/2q2qvLZ/Vector-2.png' alt='' />
                                                <h4>{work}</h4>
                                            </section>
                                        </div>
                                    </SubSectCard2>
                                )
                            })
                        }
                    </SectCard2>
                </SectInputCard>
            </main>
        )
    }
}
