import styled from "styled-components";
// ------------------------------------------------HEADER--------------------------------------------------//
export const NavbarStyled = styled.header`
background-image: url('https://i.ibb.co/vLWNHCB/iMac-2.png');
background-repeat: no-repeat; 
background-size: cover;
`
// ------------------------------------------------NAVBAR--------------------------------------------------//
export const NavPincipal = styled.nav`
display: flex;
`

export const NavIMG = styled.img`
width: 100px;
height: 30px;
margin: 50px 80px;
`

export const NavHome = styled.div`
float: right;
margin-left: 70%;
`

export const NavbarDiv = styled.div`
display: flex;
justify-content: space-evenly;
margin-left: 30rem;
width: 80%;
`
export const NavButton3 = styled.button`
background-color: #FAFAFA;
padding: 10px 16px;
height: 36px;
margin: 50px 10px;
border-radius: 8px;
border: none;

&:hover {
    background-color: #CCCCCC;
    border: 1px solid #666666;
    border-radius: 8px;
    color: #FFFFFF;
}
`

export const NavButton1 = styled.button`
border: transparent;
padding: 10px 16px;
height: 36px;
margin: 50px 10px;

&:hover {
    border: 1px solid #CCCCCC;
    border-radius: 5px;
}
`
export const NavButton2 = styled.button`
background-color: #FFFFFF;
border: 1px solid #CCCCCC;
border-radius: 5px;
padding: 10px 16px;
height: 36px;
margin: 50px 10px 0 0;


&:hover {
    background-color: #24B26B;
    color: #FFFFFF;
}

&:focus {
    background-color: #24B26B;
    color: #FFFFFF;
}
`
// ------------------------------------------------SECTION-CARD--------------------------------------------------//
export const SectCard = styled.section`
display: flex;
justify-content: space-evenly;
`
export const DivTitle = styled.div`
width: 700px;
margin: 5rem 5rem;

h1 {
    font-size: 56px;
    font-weight: 700;
    width: 475px;
    line-height: 110%;
}

p {
    margin-top: 10px;
    font-size: 16px;
    font-weight: 300;
    width: 100%;
}
`

export const DivCard = styled.div`
display: grid;
grid-template-columns: 1fr 1fr 1fr;
justify-items: center;
text-align: center;
gap: 20px;
margin-bottom: 30px;
margin-right: 55px;

section {
    background-color: #FFFFFF;
    border-radius: 8px;
    padding: 5%;
    filter: drop-shadow(0px 4px 20px rgba(0, 0, 0, 0.2));
}

section:hover {
    background-color: #F8F8F8;
}

section img {
    width: 80px;
    height: 80px;
    margin-top: 30px; 
}

section h2 {
     font-size: 14px;
     font-weight: 600;
     margin-top: 10px;
}

section h4 {
    font-size: 12px;
    font-weight: 400;
    margin-bottom: 10px;
}

section p {
    font-size: 12px;
    font-weight: 300;
    color: #666666;
    margin-bottom: 30px;
}
`
// ------------------------------------------------MAIN--------------------------------------------------//
export const SectMain = styled.section`
display: flex;
justify-content: space-between;
margin: 30px 10%;

p {
    width: 160px;
    color: #666666;
    font-size: 14px;
    font-weight: 400;
}

div {
    display: flex;
}

img {
    margin: auto 10px;
    height: 26px;
}
`

export const SectInputCard = styled.section`
text-align: center;

.tituInput h2 {
    font-size: 32px;
    font-weight: 400;
}

.tituInput p {
    font-size: 16px;
    font-weight: 400;
}

.sectInput {
    display: flex;
    justify-content: center;
    margin-top: 20px;
}

.sectInput div {
    display: flex;
    background-color: #FFFFFF;
    border: 1px solid #CCCCCC;
    border-radius: 4px;
    width: 382px;
    height: 40px;
    margin-right: 10px;
}

.sectInput img {
    margin: auto 30px auto 10px ;
    width: 24px;
    height: 24px;
}

.sectInput img:hover, img:focus {
    margin: auto 30px auto 10px ;
    width: 26px;
    height: 26px;
}

.sectInput button {
    width: 90px;
    background-color: #333333;
    border: none;
    border-radius: 4px;
    color: #FFFFFF;
}

.sectInput button:hover, button:focus {
    border: 2px solid #CCCCCC;
    background-color: #FFFFFF;
    color: #333333;
}
`

export const Input = styled.input`
border: none;
height: 70%;
width: 254px;
font-size: 16px;
font-weight: 300;
margin: 6px 0px;

&:hover {
    border-radius: 8px;
}

&:focus {
    border-radius: 8px;
}
`

export const SectCard2 = styled.div`
display: grid;
grid-template-columns: 1fr 1fr 1fr 1fr;
justify-items: center;
text-align: center;
gap: 15px;
margin: 50px 6% 0px;
`

export const SubSectCard2 = styled.section`
background-color: #FFFFFF;
box-shadow: 0px 0px 4px 4px rgba(0, 0, 0, 0.04);
border-radius: 8px 8px 0px 0px;
text-align: center;

h2 {
    font-size: 16px;
    font-weight: 600;
    margin: 10%;
}

.cards2 {
    display: flex;
    justify-content: space-evenly;
}

.subCards2 {
    display: flex;
}

img {
    height: 16px;
    margin-bottom: 20px;
    margin-right: 5px;

}

h4 {
    font-size: 12px;
    font-weight: 400;
    
}
`
// ------------------------------------------------LOGIN--------------------------------------------------//
export const BodyLogin = styled.div`
background-color: #eeeeee;
height: 100vh;
`

export const Form = styled.div`

h2{
    text-align: center;
    font-size: 40px;
}

input {
    display: block;
    border: none;
    border-radius: 8px;
    font-size: 24px;
    width: 30%;
    height: 60px;
    margin: 2% auto;
    align-content: center;
}

form button {
    margin: 0px 46%; 
}
`