import axios from 'axios'
import React, { useState } from 'react'
import Swal from 'sweetalert2'
import Navbar2 from '../components/Navbar2'
import { BodyLogin, Form, NavButton3 } from '../styles/stylesGlobal'
const UrlUsuarios = 'http://localhost:4000/Usuarios'


export default function Login() {

    const [nombre, setNombre] = useState("");
    const [apellido, setApellido] = useState("");
    const [correo, setCorreo] = useState("");
    const [contraseña, setContraseña] = useState("");

    const cambiarNombre = (e) => {
        setNombre(e.target.value);
    }
    const cambiarApellido = (e) => {
        setApellido(e.target.value);
    }
    const cambiarCorreo = (e) => {
        setCorreo(e.target.value);
    }
    const cambiarContraseña = (e) => {
        setContraseña(e.target.value);
    }

    const enviarDatos = async (e) => {
        e.preventDefault();

        let data = {
            nombre,
            apellido,
            correo,
            contraseña
        }

        await axios.post(UrlUsuarios, data);
        Swal.fire({
            position: 'top-end',
            icon: 'success',
            title: 'Felicidades usuario registrado'
          })
        setTimeout(() => {
            window.location.href = '/'
          }, 1000);
          
    }

    return (
        <BodyLogin>
            <Navbar2 />
            <Form>
                <h2>Ingresa tus Datos</h2>
                <form onSubmit={enviarDatos}>
                    <input type="text" placeholder='Nombre' value={nombre} required onChange={cambiarNombre} />
                    <input type="text" placeholder='Apellido' value={apellido} required onChange={cambiarApellido} />
                    <input type="email" placeholder='Correo Electronico' value={correo} required onChange={cambiarCorreo} />
                    <input type="password" placeholder='Contraseña' value={contraseña} required onChange={cambiarContraseña} />
                    <NavButton3 type='submit'>Registrarse</NavButton3>
                </form>
            </Form>
        </BodyLogin>
    )
}