import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import { NavbarDiv, NavButton1, NavButton2, NavIMG, NavPincipal } from '../styles/stylesGlobal'

export default class Navbar extends Component {
    render() {
        return (
            <NavPincipal>
                <NavIMG src='https://i.ibb.co/jDmPtMp/logo.png' alt='' />
                <NavbarDiv>
                    <section>
                        <NavButton1>Job Search</NavButton1>
                        <NavButton1>About</NavButton1>
                        <NavButton1>Pricing</NavButton1>
                        <NavButton1>Contact</NavButton1>
                    </section>

                    <section>
                        <Link to="/login"><NavButton2>Log in</NavButton2></Link>
                        <NavButton2>Try it Free</NavButton2>
                    </section>
                </NavbarDiv>
            </NavPincipal>
        )
    }
}
